//员工管理  的员工信息
import http from '../httpConfig'

const table = "mrbase_sys_staffs"
const table1 = 'mrbase_sys_staff_personal_info'

// 员工登录账号冻结接口
export const freezeStaff = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/staff-manager/staff-freezAccount/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

