<template>
  <v-card>
    <v-card-text>
      <mrc_ess_staffMsg v-model="list.apply_staff_uuid"
                        :iconShow='true'
                        :disabled="disabled"
                        @changeList="changeList"></mrc_ess_staffMsg>
      <div style="background-color: #f5f5f5"
           class="mt-4"
           v-if="list.apply_staff_uuid">
        <v-col cols="6">
          <v-text-field :label="$t('label_The_original_company')"
                        v-model="company_Name"
                        :readonly="disabled">
          </v-text-field>
        </v-col>
        <v-col cols="6">
          <mrc_ess_prompt :label="$t('label_The_original_department')+'*'"
                          :rules='false'
                          :disabled='true'
                          v-model="viewList.dept_uuid"
                          :obj="deptuuid">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_The_original_position')"
                          :rules='false'
                          :disabled='true'
                          v-model="viewList.job_post_uuid"
                          :obj="job_post_uuid">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="12">
          <!-- <mrc_ess_staffMsg v-model="viewList.direct_leader_code"
                          :disabled='true'
                          :staffText='staffText'></mrc_ess_staffMsg> -->
          <mrc_ess_prompt :label="$t('label_Former_boss')+'*'"
                          :rules='false'
                          :disabled='true'
                          v-model="viewList.direct_leader_code"
                          :obj="superior">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="12">
          <mrc_ess_prompt :label="$t('label_The_original_role')+'*'"
                          :rules='false'
                          :disabled='true'
                          v-model="viewList.role_uuids"
                          :obj="spica">
          </mrc_ess_prompt>
        </v-col>
      </div>
      <div>
        <v-col cols="6">
          <mrc_ess_datePicker @getTime='paramsChange'
                              :errMsg="$errors('list','change_effective',requiredRule)"
                              :disabled="disabled"
                              :label="$t('label_The_effective_date')+'*'"
                              v-model="list.change_effective"></mrc_ess_datePicker>
        </v-col>
        <v-col cols="6">
        </v-col>
        <v-col cols="12">
          <v-btn rounded
                 color="#999999"
                 dark
                 x-small
                 v-if="list.result_status==''">{{$t('label_Unchanged')}}</v-btn>
          <v-btn rounded
                 color="#13a671"
                 dark
                 x-small
                 v-if="list.result_status==1">{{$t('label_Complete')}}</v-btn>
          <v-btn rounded
                 color="#13a671"
                 dark
                 x-small
                 v-if="list.result_status==2">{{$t('label_The_task_processing_is_complete')}}</v-btn>
        </v-col>
        <v-col cols="12">
          <mrc_ess_dictName v-model="list.dimission_reason"
                            :disabled='disabled'
                            :errMsg="$errors('list','dimission_reason',requiredRule)"
                            :rules="false"
                            :label="$t('label_Reason_for_separation')"
                            dict_name='reason_for_resignation'
                            chips>
          </mrc_ess_dictName>
        </v-col>
        <v-col cols="12">
          <mrc_ess_multiText :label="$t('label_note')"
                             :isNational="judegNational('remark')"
                             v-model="list.remark"
                             :readonly="disabled">
          </mrc_ess_multiText>
        </v-col>
      </div>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
  </v-card>
</template>

<script> 
import {
  getStaffLeaveList
  , addStaffLeave
  , updateStaffLeave
  , deleteStaffLeave
  , detailStaffLeave
} from '@/service/api/staffmsg.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      list: {},
      btnIsLoading: false,
      viewList: {},
      staff: '12,121',
      staff1: '12'
    };
  },
  computed: {
    staffText () {
      return [this.$t('label_Former_boss') + '*', '']
    },
    deptuuid () {  //部门参照查询字段
      return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.company_uuid }
    },
    company_uuid () { //当前选中公司
      return this.$store.state.currentCompany
    },
    company_Name () { //当前选中公司名字
      const items = this.$store.state.topcompanylist.filter(item => item.uuid == this.$store.state.currentCompany)
      return items[0].org_level_name
    },
    job_post_uuid () {  //岗位查询
      return { prompt_name: 'level-dept-post', dept_uuid: this.viewList.dept_uuid }
    },
    superior () { //上司高级参照
      return { prompt_name: 'boss', org_level_uuid: this.company_uuid, staff_uuid: this.$store.state.uuid }
    },
    spica () { //角色高级参照 
      return { prompt_name: 'level-role', org_level_uuid: this.company_uuid }
    }
  },
  created () { },
  watch: {},
  async mounted () {
    await this.national('mrbase_sys_change_dimission')
    if (this.$store.state.uuid) {
      this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    assdffas () {
      console.log(this.list.staff);
    },
    paramsChange () { },
    changeList (val) {
      this.viewList = val
    },
    async getDetail () {
      const res = await detailStaffLeave({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      if (this.$store.state.uuid) {
        updateStaffLeave(this.$deletefield(null, this.list)).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        addStaffLeave(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'staffLeave' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'staffLeave'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      change_effective: {
        required
      },
      dimission_reason: {
        required
      },
      apply_staff_uuid: {
        required
      }
    }
  },
};
</script>

